.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.black-nav {
  display:flex;
  background: #AE899D;
  width: 100%;
  color: white;
  padding-left: 50%;
  
}

.list{
  display:flex;
  background-color:#EAB9D3;
  width:100%;
  padding: 5%;
  margin: 10px;
  font-size:20px;
  color:rgb(0, 0, 0);
  /* text-align:center; */
  line-height: 100px;
}

.likes_number{
  color:white;
  font-size:25px;
  padding-bottom:50px;
}

.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}